}

.charts-inputs {
    width: 10%;
    display: inline-block;
}

.chart-legend {
	position: absolute;
	left: 4px;
	top: 2px;
	z-index: 2;
	font-size: 18px;
	line-height: 28px;
	font-weight: 300;
    color: black;
}

.chart-legend-qty {
    position: absolute;
	left: 92px;
	top: 2px;
	z-index: 2;
	font-size: 18px;
	line-height: 28px;
	font-weight: 300;
    color: black;
}

.chart-legend-0 {
	position: absolute;
	left: 4px;
	top: 2px;
	z-index: 2;
	font-size: 14px;
	line-height: 16px;
	font-weight: 300;
    color: black;
}

.chart-legend-values {
	position: absolute;
	left: 4px;
	top: 18px;
	z-index: 2;
	font-size: 14px;
	line-height: 16px;
	font-weight: 300;
    color: black;
}

